
.header {
  padding: 9px 12px;
  border-bottom: #FAFAFA solid 1px;
  box-shadow: 0 1px 4.5px 0 rgba(0, 0, 0, 0.18);
  box-sizing: border-box;
}
.header > img:first-child {
    width: 45px;
    margin-right: 10.5px;
    float: left;
}
.header > p {
    float: left;
    width: calc(100vw - 159px);
    max-width: 341px;
    text-align: left;
}
.header > a {
    float: left;
    background: #FC4083;
    border-radius: 4px;
    color: #fff;
    width: 69.5px;
    height: 30px;
    line-height: 30px;
    margin-left: 10px;
    margin-top: 7.5px;
}
.content {
  top: 64px;
}
.userinfo {
  padding: 12px;
  overflow: hidden;
}
.userinfo > img {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    float: left;
    margin-right: 8px;
}
.userinfo .video_desc {
    width: calc(100vw - 102px);
    max-width: 398px;
    float: left;
}
.userinfo .video_desc h3 {
      font-size: 16px;
      font-weight: 600;
}
.userinfo .video_desc p {
      color: #999;
}
.vip_show {
  position: relative;
}
.vip_show > p {
    font-size: 16px;
    padding: 0 12px 7.5px;
}
.vip_show .video_banner {
    position: relative;
}
.vip_show .video_banner > img:first-child {
      display: block;
      width: 100%;
      height: calc(100vw*11/9);
      max-height: 611.11111px;
      background: #eee;
}
.vip_show .video_banner > img:last-child {
      display: block;
      position: absolute;
      left: 12px;
      top: 8px;
}
.vip_show .vip_download {
    position: absolute;
    bottom: 12px;
    left: 50%;
    margin-left: -150px;
    text-align: center;
    color: #fff;
}
.vip_show .vip_download a {
      display: block;
      color: #fff;
      width: 300px;
      height: 44px;
      line-height: 44px;
      opacity: 0.9;
      background: #FC4083;
      border-radius: 4px;
      font-size: 14px;
      margin-top: 7.5px;
}
.download {
  display: block;
  background: #FC4083;
  border-radius: 4px;
  width: 80%;
  margin: 20px	 auto;
  color: #fff;
  height: 44px;
  line-height: 44px;
  font-size: 14px;
  text-align: center;
}
.more_video {
  padding: 0 12px;
}
.more_video h3 {
    font-size: 16px;
}
.more_video ul {
    overflow: hidden;
    padding-bottom: 10px;
}
.more_video ul li {
      float: left;
      width: calc((100vw - 36px)/2);
      background: #FAFAFA;
      box-shadow: 0 2px 7px 0 rgba(201, 201, 201, 0.75);
      border-radius: 2px;
      margin-bottom: 11.5px;
}
.more_video ul li:nth-child(2n) {
        margin-left: 12px;
}
.more_video .video_bigImg {
    position: relative;
}
.more_video .video_bigImg .video_poster {
      display: block;
      width: 100%;
      height: calc((100vw - 36px)/2 * 11/9);
}
.more_video .video_bigImg .btn_play {
      position: absolute;
      left: 50%;
      top: 50%;
      margin-left: -12.5px;
      margin-top: -12.5px;
}
.more_video .video_bigImg > div {
      position: absolute;
      bottom: 12px;
      right: 12px;
}
.more_video .video_bigImg > div .time_bg {
        display: block;
}
.more_video .video_bigImg > div .time_play {
        position: absolute;
        left: 7px;
        top: 6px;
}
.more_video .video_bigImg > div span {
        position: absolute;
        left: 13px;
        top: 0;
        color: #fff;
        font-size: 10px;
        -webkit-transform: scale(0.8);
}
.more_video .video_content {
    padding: 3px 7px;
    font-size: 14px;
    line-height: 20px;
}
.vjs_video_417-dimensions {
  height: auto !important;
}
.vjs_video_417-dimensions video {
    position: inherit !important;
}
.vjs_video_3-dimensions {
  height: auto !important;
}
.video-js .vjs-tech {
  position: inherit !important;
}
.vjs-playback-rate-value {
  display: none;
}
.video-js.vjs-custom-skin .vjs-control-bar .vjs-fullscreen-control {
  display: none;
}
.video-player .video-js.vjs-custom-skin .vjs-big-play-button {
  width: 41px !important;
  height: 41px !important;
  line-height: 41px !important;
  border-radius: 50%;
  border: 1px solid #fff;
  margin-left: -20.5px;
  margin-top: -20.5px;
}



